/* src/styles/Contact.css */

#contact {
  background-color: rgba(58, 197, 231, 0.7); /* Couleur de fond gris clair */
  padding: 20px; /* Espacement interne pour le contenu */
  border-radius: 8px; /* Arrondir les coins */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5); /* Ombre légère pour l'effet de profondeur */
  margin-top: 20px; /* Espace au-dessus du conteneur */
}


