
body,html {
    background: url('./images/background.jpeg') no-repeat center center fixed;
    background-size: cover; 
    margin: 0;
    height: 100%;
    overflow: hidden; /* Empêche la barre de défilement sur le corps */
    box-sizing: border-box;
  }

  #root {
    height: 100%;
    font-family: 'Poppins', sans-serif;
    text-align: center;
    color : white; 
  }
  
  .app {
    display: flex;
    flex-direction: column;
    height: 100%; /* Assure que .app prend toute la hauteur disponible */
  }

  main {
    flex: 1; /* Permet à cette section de grandir pour remplir l'espace restant */
    background-color: rgba(0, 0, 0, 0.5); /* Filtre noir avec 50% d'opacité */
    margin-top: 4rem; /* Si nécessaire pour espacement supplémentaire */
    margin-bottom: 2.5rem; /* Si nécessaire pour espacement supplémentaire */
  }
  @keyframes fadeIn {
    0% { opacity: 0; }
    100% { opacity: 1; }
}

@keyframes fadeOut {
    0% { opacity: 1; }
    100% { opacity: 0; }
}
  

/* Importation de la police Poppins depuis Google Fonts */
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&display=swap');

.Heading {
  font-size: 3rem;
  margin-bottom: 0;
  animation: fadeInUp 0.5s ease-out forwards;
}

.Subheading {
  font-size: 1.7rem;
  margin-top: 0;
  animation: fadeInUp 0.5s ease-out 0.6s forwards;
}

.Body1 {
  font-size: 1.5rem;
  animation: fadeInUp 0.5s ease-out 0.9s forwards;
}

.Body2 {
  font-size: 1.25rem;
  animation: fadeInUp 0.5s ease-out 0.9s forwards;
}



/* Styles pour les écrans de taille moyenne */
@media (max-width: 1280px) {
  .Heading {
    font-size: 2.8rem;
  }

  .Subheading {
    font-size: 1.6rem;
  }

  .Body1 {
    font-size: 1.375rem;
  }

  .Body2 {
    font-size: 1.125rem;
  }
}

/* Styles pour les écrans de taille petite */
@media (max-width: 960px) {
  .Heading {
    font-size: 2.7rem;
  }

  .Subheading {
    font-size: 1.5rem;
  }

  .Body1 {
    font-size: 1.25rem;
  }

  .Body2 {
    font-size: 1rem;
  }
}

/* Styles pour les très petits écrans (mobiles) */
@media (max-width: 600px) {
  .Heading {
    font-size: 2rem;
  }

  .Subheading {
    font-size: 1.5rem;
  }

  .Body1 {
    font-size: 1rem;
  }

  .Body2 {
    font-size: 0.875rem;
  }
}
